.lineTextColorsForDarkTheme {
    background-color: rgb(215, 215, 231);
    color: rgb(80, 72, 72);
    
}
.lineTextColorsForLightTheme {
    background-color: rgb(57, 57, 63);
    color: rgb(230, 211, 211);
}
.lineTextDecoration {
    border: 1px solid black;
    border-radius: 10px;
    padding-left: 10px;
    padding-right: 10px;
    min-height: 25px;

    /* margin: 10px; */

    width: 280px;
    height: 40px;
}
.error {
    background-color: rgb(219, 95, 95);
}