.dataBricksWrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
}
.brickHead {
    display: grid;
    grid-template-columns: 3fr 1fr 1fr 1fr;
}
.brickHeadPeice {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.oneBrick {
    display: grid;
    grid-template-columns: 3fr 1fr 1fr 1fr;

    border: 2px black solid;
    border-radius: 10px;
    padding: 10px;

    background-color: rgb(239, 181, 142);

}

.oneBrickPiece {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;    
}