.header {
    color: var(--font_color);
    /* display: flex; */
    /* flex-direction: row; */
    /* align-items: center; */
    /* justify-content: flex-end; */
    /* gap: 30px; */

    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    padding: 10px 20px 10px 20px;
    
    background-color: var(--header_footer_bgColor);
}

.icons_div {
    display: flex;
    flex-direction: row;
    gap: 15px;
    align-items: center;
    justify-content: flex-end;
}
.simpleDiv {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.toogler_div {
    width: 40px;
    height: 20px;
    border: 2px black solid;

    background-color: var(--theme_toogler_bgColor);

    border-radius: 10px;
}

.logoImg {
    width: 140px;
    height: 70px;
    box-shadow: 5px 5px grey;
    border-radius: 5px;
}
.iconsImg {
    width: 40px;
    height: 40px;
    cursor: pointer;
}
.avaStyle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}